@import "@wangeditor/editor/dist/css/style.css";
@import "highlight.js/styles/default.css";
.eidtor-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.editor {
  flex: 2;
}
.priview-wrap {
  min-height: 100%;
  border: 1px solid #555;
  padding: 24px;
}
.priview-wrap > p {
  text-indent: 3ch;
}
.priview-wrap p img {
  width: 98%;
  height: 100%;
}
.priview-wrap video {
  width: 100%;
  height: 100%;
}
